<div class="user-content" v-if="user"> <div class="user-content-left"> <img :src="user.avatar_url" alt="alt"><a :href="user.html_url">Посетить</a></div><div class="user-content-right"> <h2>Имя: <span>{{ user.login }}</span></h2><p>Репозиториев: <span>{{user.public_repos}}</span> </p><p>Создан:  <span>{{new Date(user.created_at).toLocaleDateString() }}</span> </p><p>Подписчиков: <span>{{user.followers}}</span> </p><p>Подписок: <span>{{user.following}}</span> </p></div></div><div class="user-content" v-else> <div class="user-content-left"> <img src="https://res.cloudinary.com/apideck/image/upload/v1598543613/icons/github.png" alt="alt"><a href="">Посетить</a></div><div class="user-content-right"> <p>Удобно смотри github акаунты знакомых</p></div></div><div class="sort"> <div class="sort-box"> <button class="sort-box__btn" v-for="(btn, index) in btns" :key="btn.name" :class="{active: isActive == index}" @click="sort(index, btn.sortType)">{{ btn.name }}</button></div></div>